html{
    height: 100%;
    width: 100%;
}
body {
    margin: 0;
    padding: 0;

    font-size: 200%;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    background-repeat: repeat;
    background-image: url('http://static.allbackgrounds.com/th2/black-stars.jpg');
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.base{
       max-width: 1200px;
        margin: auto;
   }



.Loading{
    bottom: 15%;
    height: auto;
    left: 20%;
    position: absolute;
    right: 20%;
    top: 15%;
    width: auto;
    text-align: center;
    align-content: center;
    background: black;
}
#place-loading{
    position: absolute;
    background: black;
    opacity: 90%;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

#ava-min{
    height: 100px;
    width: 100px;
    border-radius: 20px;
}
.Panel-Ava{
    height: 100px;
    width: 100px;
}

.img-but{
    width: 30px;
    height: 30px;
    margin: 0;
    position: relative;
    top: 3px;
}


